*, *:after, *:before {
  box-sizing: border-box;
}

body {
  margin:0;
  font-family: "Nunito Sans", sans-serif;
}


.container{
  max-width: 1350px;
  margin:0 auto;
}

.main-love-music{

}

.section-header-text{
  font-size:20px;
}

.section-header-title{
  font-size:20px;
  text-transform: uppercase;
  padding-bottom: 30px;
  position:relative;
  margin-top:10px;
}

.section-header-title:after{
  display:block;
  position:absolute;
  left:50%;
  bottom: 0;
  content:'';
  height:2px;
  width:125px;
  background: $color-main;
  transform: translate(-50%);
}

.row{
  display:flex;
}

.row .col-1-3 {
  margin:0 15px;
  flex-grow:1;
}

@media (max-width: 930px){

  .row{
    flex-direction: column;
  }

  .row .col{
    margin-bottom: 40px;

  }
}

